<template>
  <div class="mt-4">
    <!-- <v-card-title> -->
      <!-- <div class="text-h5 font-weight-light"> -->
        <v-btn @click="$router.back()" class="ml-2 primary" elevation="2">
          <v-icon color="white">mdi-keyboard-backspace</v-icon>
          BACK</v-btn>
      <!-- </div> -->
    <!-- </v-card-title> -->
    <v-card class="ma-2">
      <v-card-title>
        Payment Voucher  {{ $route.params.id }}
      </v-card-title>
      <v-data-table :headers="headers" :items="state.detail" loading-text="Loading... Please wait" :loading="state.loading">
        <template #[`item.index`]="{ item }">
          {{ state.data.indexOf(item) + 1 }}.
        </template>
        <template #[`item.ApprovalDate`]="{ item }">
            {{ toDateTime(item.ApprovalDate)}}
        </template>
        <template #[`item.LoanAmount`]="{ item }">
            {{ toRupiahString(item.LoanAmount > 0 ? item.LoanAmount : '0')}}
        </template>
        <template #[`item.view`]="{ item }">
          <v-btn icon color="success" @click="show(item.pvcode)">
            <v-icon small>
              mdi-eye
            </v-icon>
          </v-btn>
        </template>
      </v-data-table>
    </v-card>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex'
import store from '@/store'
import Utils from '@/utils/util'
export default {
  name: 'FundingBatch',
  data: () => ({
    headers: [
      {
        text: 'Tgl Pengajuan',
        align: 'start',
        sortable: false,
        value: 'SubmitDate'
      },
      { text: 'No Pengajuan', sortable: false, value: 'ApprovalNo' },
      { text: 'NIK', sortable: false, value: 'EmployeeID' },
      { text: 'Nama', sortable: false, value: 'EmployeeName' },
      { text: 'Lokasi Kerja', sortable: false, value: 'WorkLocation' },
      { text: 'Pinjaman', sortable: false, align: 'right', value: 'LoanAmount' },
      { text: 'Tujuan', sortable: false, value: 'FinancePurpose' },
      { text: 'Bank', sortable: false, value: 'payrollbankname' },
      { text: 'No.Rekening', sortable: false, value: 'payrollaccountno' },
      { text: 'Payment Voucher', sortable: false, value: 'pvcode' }
    ]
  }),
  mounted: function () {
    store.dispatch('fundingbatch/show', this.$route.params.id)
  },
  computed: {
    ...mapState('fundingbatch', {
      state: (state) => state
    })
  },
  methods: {
    ...mapActions('fundingbatch', [
      'show', 'download'
    ]),
    toRupiahString (nominal) {
      return Utils.toRupiahString(nominal)
    },
    toDateTime (tanggal) {
      return Utils.toDateTime(tanggal)
    }
  }
}
</script>
